.all-pages .arrow-wrapper {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  bottom: 1rem;
  right: 1rem;
  height: 3rem;
  width: 3rem;
  background: green;
  border-radius: 50%;
  z-index: 1;
}

.all-pages .arrow-wrapper .arrow {
  display: flex;
  justify-content: center;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.all-pages .arrow-wrapper .arrow .shape {
  height: 1rem;
  width: 1rem;
  border-right: 2px solid black;
  border-bottom: 2px solid black;
  -webkit-transform: rotate(45deg) translateY(-2px) translateX(-2px);
          transform: rotate(45deg) translateY(-2px) translateX(-2px);
}

.all-pages .arrow-bottom-of-page {
  -webkit-transform: rotate(180deg) translateY(-5px);
          transform: rotate(180deg) translateY(-5px);
}
/*# sourceMappingURL=Arrow.css.map */
.projects {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: url(/ozzi_website/static/media/background.5a3e1c2a.jpg);
  background-attachment: fixed;
  margin: 0 0.2rem;
}

.projects .project {
  display: flex;
  flex: 1 1 350px;
  position: relative;
  overflow: hidden;
  margin: 1rem;
}

@media (min-width: 800px) {
  .projects .project {
    max-width: 450px;
  }
}

.projects .project .project-image {
  transition: opacity .5s;
}

.projects .project .project-image img {
  width: 100%;
}

.projects .project .project-title {
  position: absolute;
  top: 50%;
  width: 100%;
  font-size: 1.5rem;
  transition: font-size .5s;
  font-weight: 500;
  color: #6d0b41;
  cursor: default;
  display: flex;
  justify-content: center;
}

.projects .project .project-title .project-title-text {
  text-align: center;
  background: rgba(122, 138, 111, 0.7);
}

.projects .project *::-moz-selection {
  background: none;
}

.projects .project *::selection {
  background: none;
}

.projects .project:hover .project-image {
  opacity: .8;
}

.projects .project:hover .project-title {
  font-size: 2rem;
}
/*# sourceMappingURL=Projects.css.map */
.skills .skills-all-skills {
